<template>
  <v-app>

  <!-- <Drawe /> -->
  <NavBar />
  <v-main>
    <v-container
      id="container"
      fluid
      class="d-flex grey"
    >
      <v-row>
        <v-col cols="2">
          <v-img
            dark
            contain
            class="rounded-circle"
            alt="Imagem_perfil_git"
            src="https://avatars0.githubusercontent.com/u/42067419?s=400&u=a4833b5dd0f63825490467dcbf8dda910cf49cfa&v=4"
          />
           <p class="font-weight-bold text-center pa-0 ma-0">Marlon Rodrigues</p>
          <v-divider class="d-sm-none d-none" />
          <v-main class="ma-0 pa-0">
            <p>Acadêmico de Ciência da Computação, Barra do Bugres-MT</p>
            <p>Amante da linguagens JavaScript, Python e C</p>
            <p>Experiencia com HTML5, Css2, VueJs, React, React-Native, BootStrap4</p>
            <p>Procurando novas parceirias e expêriencias no ramo do Desenvolvimento</p>
          </v-main>
        </v-col>
        <v-divider vertical style="blackground-color; black"/>
        <v-col class="d-flex flex-row flex-wrap align-content-space-around">
          <v-card
            v-for="item in posts"
            :key="item.id"
            class="ms-auto my-2"
            max-width="20rem"
          >
            <v-img 
              :src="item.imagem"
              height="200px"
            />

            <v-card-title>
              {{ item.displayName }}
            </v-card-title>

            <v-card-subtitle>
              Linguagens e FrameWorks: {{ item.language }}
            </v-card-subtitle>
            <v-card-text>
              {{item.description}}
            </v-card-text>

            <v-card-actions>

              <v-btn
                color="primary"
                text
                @click="redirect(item.html_url)"
              >
                Ver mais
              </v-btn>

              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-main>
  <!-- <Footer /> -->

</v-app>
</template>

<script>
import NavBar from './components/NavBar'
import axios from 'axios'

export default {
  name: 'App',

  components: {
    NavBar,
  },

  data: () => ({
    mensagem: 'teste de mensagem',
    posts: []
  }),
  methods: {
    redirect(url) {
      window.open(`${url}`, '_blank');
    }
  },
  async created() {
    try {
      const {data} = await axios.get(`https://api.github.com/users/m4rlonr/repos`)
      this.posts = data
      this.posts.map(item => {
        item.displayName = item.name.charAt(0).toUpperCase() + item.name.slice(1).split("_").join(" ")
        item.imagem = `https://raw.githubusercontent.com/${item.owner.login}/${item.name}/master/${item.name}.png`
      })
    } catch (e) {
      console.log(e)
    }
  }
};
</script>

<style>
  #container {
    height: 100%
  }
</style>